<style scoped lang="less">
    .page {
        text-align: left;
        padding: 30px;
    }

    .pagination {
        margin-top: 20px;
        width: 100%;
        text-align: center;
    }
</style>

<template>
    <div class="page">
        <el-row class="header">

            <el-col class="filter-label" :span="2">规则编号:</el-col>
            <el-col :span="4">
                <el-input class="filter-match" v-model="data.id" placeholder="请输入"></el-input>
            </el-col>
            <el-col class="filter-label" :span="2"> 审核状态:</el-col>
            <el-col :span="4">
                <el-select class="filter-match" v-model="data.auditor" placeholder="请选择">
                    <el-option
                            v-for="item in statusOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                </el-select>
            </el-col>
            <el-col class="filter-label" :span="2"> 创建日期:</el-col>
            <el-col :span="4">
                <el-date-picker
                        style="width: 100%"
                        type="datetimerange"
                        range-separator="~"
                        v-model="data.create_time"
                        start-placeholder="开始"
                        value-format="yyyy-MM-dd HH:mm:ss"
                        format="yyyy-MM-dd"
                        end-placeholder="结束">
                </el-date-picker>
            </el-col>
            <el-col class="filter-btn" :span="6">
                <el-button icon="el-icon-search" type="primary" @click="loadData(1)">查询</el-button>
                <el-button icon="el-icon-refresh" @click="ref()">重置</el-button>
                <el-button icon="el-icon-plus" @click="add" type="primary">新建</el-button>
            </el-col>
        </el-row>
        <el-row class="filter">
            <el-col class="filter-label" :span="2"> 登录日期:</el-col>
            <el-col :span="4">
                <el-date-picker
                        style="width: 100%"
                        type="datetimerange"
                        range-separator="~"
                        v-model="data.login_time"
                        start-placeholder="开始"
                        value-format="yyyy-MM-dd HH:mm:ss"
                        format="yyyy-MM-dd"
                        end-placeholder="结束">
                </el-date-picker>
            </el-col>
            <el-col class="filter-btn" :span="6">
                <el-button icon="" @click="reviewAll()" type="primary">批量审核</el-button>
            </el-col>

        </el-row>
        <el-table
                :data="res.data"
                stripe
                @selection-change="handleSelectionChange"
                style="width: 100%">
            <el-table-column type="selection" width="55">
            </el-table-column>
            <el-table-column
                    prop="id"
                    sortable
                    label="ID">
            </el-table-column>
            <el-table-column
                    prop="mobile"
                    label="账号">
            </el-table-column>
            <el-table-column
                    sortable
                    prop="auditor"
                    label="审核状态">
                <template slot-scope="scope">
                    <span>{{scope.row.auditor===0?'未提交':scope.row.auditor===1?'未审核':scope.row.auditor===2?'通过审核':'拒绝'}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    sortable
                    prop="status"
                    label="状态">
                <template slot-scope="scope">
                    <span>{{scope.row.status===1?'正常':'禁用'}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    prop="create_time"
                    label="创建时间">
            </el-table-column>
            <el-table-column
                    prop="login_time"
                    label="最后登录时间">
            </el-table-column>

            <el-table-column
                    label="操作">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
                    <el-button v-if="scope.row.auditor===1" type="text" size="small" @click="access(scope)">通过审核
                    </el-button>
                    <el-button v-if="scope.row.auditor===1" type="text" size="small" @click="refuse(scope)">拒绝
                    </el-button>
                </template>
            </el-table-column>
        </el-table>

        <div class="pagination ">
            <el-pagination
                    background
                    layout="total,prev, pager, next,jumper"
                    :page-count="res.last_page"
                    @current-change="loadData"

            >
            </el-pagination>

        </div>
    </div>
</template>

<script>
    import api from '../../js/api/member/review'

    export default {

        data() {
            return {
                data: {
                    id: null,
                    status: null,
                    jump_page: null,
                    auditor: null,
                    create_start: null,
                    create_end: null,
                    create_time: null,
                    login_start: null,
                    login_end: null,
                    login_time: null,
                },
                statusOptions: [
                    {label: '未提交', value: 0},
                    {label: '待审核', value: 1},
                    {label: '通过审核', value: 2},
                    {label: '已拒绝', value: 3}
                    ],
                res: {},
                selectItems: [],
            }
        },
        mounted() {
            this.loadData(1);
        },
        methods: {

            ref() {
                this.data = {
                    id: null,
                    status: null,
                    jump_page: null,
                    create_start: null,
                    create_end: null,
                    create_time: null,
                    login_start: null,
                    login_end: null,
                    login_time: null,
                };
                this.loadData(1)
            },
            handleSelectionChange(val) {
                console.log(val)
                this.selectItems = val;
            },
            loadData(page) {
                if (this.data.create_time != null && this.data.create_time.length === 2) {
                    this.data.create_start = this.data.create_time[0];
                    this.data.create_end = this.data.create_time[1]
                } else {
                    this.data.create_start = null;
                    this.data.create_end = null;
                }
                if (this.data.login_time != null && this.data.login_time.length === 2) {
                    this.data.login_start = this.data.login_time[0];
                    this.data.login_end = this.data.login_time[1]
                } else {
                    this.data.login_start = null;
                    this.data.login_end = null;
                }
                this.data.jump_page = page;
                api.index(this.data, res => {
                    this.res = res.data.data;
                    console.log(this.res)
                });
            },
            add() {
                this.$router.push({name: 'reviewAdd'})
            },
            ref() {
                this.data = {
                    id: null,
                    status: null,
                    jump_page: null,
                };
                this.loadData(1)
            },
            refuse(scope) {
                this.$prompt('请输入拒绝' + scope.row.mobile + '理由', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                }).then(({value}) => {
                    this.review({id: [scope.row.id], type_id: 0, reason: value})
                }).catch(() => {
                });
            },
            access(scope) {
                this.review({id: [scope.row.id], type_id: 1})
            },

            review(data) {
                api.auditor(data, () => {
                    this.$message.success('操作成功')
                    this.loadData(1)
                })
            },
            reviewAll() {
                if (this.selectItems.length === 0) {
                    this.$message.info('请至少勾选一个条目')
                    return;
                }
                let ids = [];
                this.selectItems.forEach(value => {
                    ids.push(value.id);
                })
                this.review({id: ids, type_id: 1})
            },
            handleClick(item) {
                this.$router.push({
                    name: 'reviewAdd', query: {
                        id: item.id
                    }
                })
            }
        }
    }
</script>
