import Api from '.././baseApi'

export default class reviewApi {
    static index(data, callback, errorBack) {
        Api.request(Api.url() + 'user2/' + 'wait_auditor_entry', callback, errorBack, data)
    }

    static save(data, callback, errorBack) {
        Api.request(this.uri() + 'save', callback, errorBack, data)
    }

    static detail(data, callback, errorBack) {
        Api.request(this.uri() + 'introduce', callback, errorBack, data)
    }

    static auditor(data, callback, errorBack) {
        Api.request(this.uri() + 'auditor', callback, errorBack, data)
    }

    static uri() {
        return Api.url() + 'user/'
    }

}
